@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
.header-container{width:100%;display:inline-block;position:fixed;top:0;background-color:white}.nav-bar{overflow:hidden;position:relative;width:100%}.nav-bar .nav-list{display:-webkit-flex;display:flex;text-align:center;list-style-type:none;margin:0;padding:25px;-webkit-justify-content:space-between;justify-content:space-between}.nav-bar .nav-list .nav-items{padding-top:15px}.nav-bar .nav-list .nav-button{display:inline-block;font-size:18px}.nav-bar .nav-list .nav-button .logo{font-family:"Dancing Script", cursive;font-size:2rem}.nav-bar .nav-list .nav-button a{color:#333;text-decoration:none;padding:25px 10px}.nav-bar .nav-list .nav-button a:hover{background-color:#f5f5f5;border-radius:5px}

.homepage-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;width:100%}.homepage-container .hero{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;background:url(/LuPo-photo/static/media/landscape0.116ef932.jpeg) no-repeat center center fixed;background-size:cover;height:200vh;width:95%;-webkit-align-items:center;align-items:center;margin-top:30px}.homepage-container .hero .homepage-title{color:white;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;text-align:center;-webkit-justify-content:center;justify-content:center;margin:0;height:100vh}.homepage-container .hero .homepage-title .homepage-name{font-family:"Dancing Script", cursive;font-size:5rem;font-weight:100;margin:0}.homepage-container .hero .homepage-title .homepage-subtitle{font-size:3rem;margin:0}

.about-container{width:80%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;text-align:center}.about-container .about-content{display:-webkit-flex;display:flex;height:100%;grid-gap:10px;gap:10px}@media screen and (max-width: 800px){.about-container .about-content{-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}}.about-container .about-content .about-image{display:-webkit-flex;display:flex;height:auto}.about-container .about-content .about-image img{object-fit:cover;width:100%;height:auto}@media screen and (max-width: 800px){.about-container .about-content .about-image{margin-left:auto;margin-right:auto}}.about-container .about-content .about-text{text-align:left;width:50%;margin:10px}@media screen and (max-width: 800px){.about-container .about-content .about-text{width:80%}}

.portfolio-container{width:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.portfolio-container .portfolio-title{padding:50px}.portfolio-container .portfolio-picture-container{width:80%;display:grid;grid-gap:10px;grid-template-columns:repeat(auto-fit, minmax(25vmin, 1fr));grid-auto-rows:25vmin;grid-auto-flow:row dense}.portfolio-container .portfolio-picture-container .normal{overflow:hidden;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;transition:-webkit-transform 0.5s;transition:transform 0.5s;transition:transform 0.5s, -webkit-transform 0.5s}.portfolio-container .portfolio-picture-container .normal:hover{-webkit-transform:scale(2);transform:scale(2)}.portfolio-container .portfolio-picture-container .normal img{width:auto;height:100%}.portfolio-container .portfolio-picture-container .landscape{grid-column:span 2;overflow:hidden;width:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;transition:-webkit-transform 0.5s;transition:transform 0.5s;transition:transform 0.5s, -webkit-transform 0.5s}.portfolio-container .portfolio-picture-container .landscape:hover{-webkit-transform:scale(2);transform:scale(2)}.portfolio-container .portfolio-picture-container .landscape img{width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.portfolio-container .portfolio-picture-container .portrait{overflow:hidden;grid-row:span 2;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;transition:-webkit-transform 0.5s;transition:transform 0.5s;transition:transform 0.5s, -webkit-transform 0.5s}.portfolio-container .portfolio-picture-container .portrait:hover{-webkit-transform:scale(2);transform:scale(2)}.portfolio-container .portfolio-picture-container .portrait img{height:100%;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}

.App{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}

html{font-family:"Roboto", sans-serif}

